import React, { useEffect, useState } from 'react';
import { Modal, Table, Space, Input, Button, message } from 'antd';
import { labelTemplatesGet, labelTemplateGet, labelTemplateDelete } from './requests';

const { Search } = Input;

const makeTabelColumns = (setVisible, selectedLabel, setSelectedLabel, labels, setLabels) => {
    // label
    const pullLabelObj = (labelId) => {
        // 
        labelTemplateGet(labelId).then(res => {
            // console.log(res);
            if (res.label_object) {
                const labObj = { ...res.label_object, labid: res.id ? res.id : 0, }
                setSelectedLabel(labObj);
                setVisible(0)
            } else {
                message.error('...');
                setSelectedLabel({});
            }
        }).catch(err => {
            console.log(err);
            message.error(', ...');
        });
    };

    const selectLabelHandle = (text, record, index) => {
        // 
        console.log(text, record, index);
        const labelObj = record.currentLabel;
        console.log(labelObj);
        if (labelObj && labelObj.id) {
            pullLabelObj(labelObj.id);
        } else {
            message.error('...');
        }
    };

    const deleteLabelHandle = (text, record, index) => {
        // 
        console.log(text, record, index);
        const labelObj = record.currentLabel;
        if (labelObj && labelObj.id) {
            labelTemplateDelete(labelObj.id).then(res => {
                message.success(' ' + labelObj.label_title);
                const del_labels = [].concat(labels);
                del_labels.splice(index, 1);
                setLabels(del_labels);
            }).catch(err => {
                console.log(err);
                message.error('')
            });
        }
    };

    return [
        { title: '', dataIndex: 'labelTitle', key: 'labelTitle', width: 160, editable: false },
        {
            title: '',
            dataIndex: 'action',
            key: 'action', width: 160,
            render: (text, record, index) => (
                <Space size="middle">
                    <Button type="link" onClick={() => selectLabelHandle(text, record, index)}></Button>
                    <Button type="link" onClick={() => deleteLabelHandle(text, record, index)}></Button>
                </Space>
            ),
        },
    ]
}

const makeSingleRowObj = (rowObj, index) => {
    // 
    return {
        key: index.toString(),
        labelTitle: rowObj.label_title,
        id: rowObj.id,
        currentLabel: rowObj,
    };
}

const makeLabelData = (response) => {
    // 
    const tableArr = [];
    const results = response.results;
    for (let index = 0; index < results.length; index++) {
        const element = results[index];
        tableArr.push(makeSingleRowObj(element, index));
    }
    return tableArr;
}

const ModalLabelsList = ({ visible, setVisible, showVisibleNum, selectedLabel, setSelectedLabel, commonOrLensLabel }) => {
    // 
    const [nextPage, setNextPage] = useState(1);
    const [labels, setLabels] = useState([]);
    const [queryFactor, setQueryFactor] = useState({});

    const [loading, setLoading] = useState(false);
    const [searchValue, setSearchValue] = useState('');
    const [totalLabelCount, setTotalLabelCount] = useState(0)

    const updateSearchVal = e => {
        setSearchValue(e.target.value);
    }
    
    const loadLabelData = () => {
        // 
        setLoading(true);
        labelTemplatesGet(nextPage, queryFactor).then(res => {
            console.log(res);
            setTotalLabelCount(res.count ? res.count : 0);
            const labelData = makeLabelData(res);
            console.log(labelData);
            setLabels(labelData);
            setLoading(false);
        }).catch(err => {
            console.error(err);
            setLabels([]);
            setLoading(false);
        });
    };

    const initLableData = (currentQueryVal = '') => {
        // label
        setNextPage(1);
        setQueryFactor({ label_title: currentQueryVal, is_lens: commonOrLensLabel });
    }

    useEffect(() => {
        // 
        if (nextPage === 1) {
            setLabels([]);
            loadLabelData();
        } else {
            setNextPage(1);
        }
    }, [queryFactor]);

    useEffect(() => {
        // 
        setLabels([]);
        loadLabelData();
    }, [nextPage]);

    useEffect(() => {
        if (visible > 0) {
            setSearchValue('');
            setQueryFactor({ label_title: '', is_lens: commonOrLensLabel });
        }
    }, [visible]);

    const paginationChange = (page, pageSize) => {
        console.log(page);
        setNextPage(page);
    }

    return (
        <Modal
            title=""
            visible={showVisibleNum === visible}
            onCancel={() => setVisible(0)}
        >
            <Space direction="vertical">
                <Search placeholder="input search text" value={searchValue} onChange={updateSearchVal} onSearch={(val) => initLableData(val)} enterButton loading={loading} />
                <Table
                    columns={makeTabelColumns(setVisible, selectedLabel, setSelectedLabel, labels, setLabels)}
                    dataSource={labels}
                    pagination={{onChange: paginationChange, total: totalLabelCount, pageSize: 10}}
                ></Table>
            </Space>
        </Modal>
    );
};

ModalLabelsList.defaultProps = {
    visible: 0,
    setVisible: num => console.log(num),
    showVisibleNum: 1,
    selectedLabel: {},
    setSelectedLabel: obj => console.log(obj),
    commonOrLensLabel: 0,
    selectedLabelID: 0,
    setSelectedLabelID: id => console.log(id),
};

export { ModalLabelsList };
