const clodopUrl = urls.clodopUrl;
console.log(clodopUrl);
//==CLodopJS,800018000():==
function loadCLodop() {
    if (CLodopJsState === "loading" || CLodopJsState === "complete") return;
    CLodopJsState = "loading";
    if (typeof window !== 'undefined' && typeof document !== 'undefined') {
        var protocol = window.location.protocol;    //httphttps

        var insertbody = document.body || document.getElementsByTagName("script")[0] || document.documentElement;
        var JS1 = document.createElement("script");
        var JS2 = document.createElement("script");
        if(protocol === 'https:'){
            JS1.src = `https://localhost.lodop.net:8443/CLodopfuncs.js`;
            JS2.src = `https://localhost.lodop.net:8444/CLodopfuncs.js`;
        }else {
            try {
                JS1.src = `http://${clodopUrl}:8000/CLodopfuncs.js?priority=1`;
            } catch (error) {
                console.error(error);
            }
            JS2.src = `http://${clodopUrl}:18000/CLodopfuncs.js`;
        }
        JS1.onload  = JS2.onload  = function()    {CLodopJsState = "complete";}
        JS1.onerror = JS2.onerror = function(evt) {CLodopJsState = "complete";}
        JS1.type = JS2.type = "text/javascript";
        // console.log(JS2);
        try {
            document.body.appendChild(JS1);
            document.body.appendChild(JS2);
        } catch (error) {
            console.log(error);
        }
        CLodopIsLocal = !!((JS1.src + JS2.src).match(/\/\/localhost|\/\/127.0.0.1/i));
    }
}

if (needCLodop()){loadCLodop();}//

//==LODOP,:==
function getLodop(oOBJECT, oEMBED) {
    var strHtmInstall = "<br /><font color='#FF00FF'>!<a href='install_lodop32.exe' target='_self'></a>,</font>";
    var strHtmUpdate = "<br /><font color='#FF00FF'>!<a href='install_lodop32.exe' target='_self'></a>,</font>";
    var strHtm64_Install = "<br /><font color='#FF00FF'>!<a href='install_lodop64.exe' target='_self'></a>,</font>";
    var strHtm64_Update = "<br /><font color='#FF00FF'>!<a href='install_lodop64.exe' target='_self'></a>,</font>";
    var strHtmFireFox = "<br /><br /><font color='#FF00FF'>LodopnpActiveXPLugin,->-></font>";
    var strHtmChrome = "<br /><br /><font color='#FF00FF'>(</font>";
    var strCLodopInstall_1 = "<br /><font color='#FF00FF'>WebCLodop<a href='CLodop_Setup_for_Win32NT.exe' target='_self'></a>";
    var strCLodopInstall_2 = "<br /><a href='CLodop.protocol:setup' target='_self'></a>";
    var strCLodopInstall_3 = "</font>";
    var strCLodopUpdate = "<br /><font color='#FF00FF'>WebCLodop!<a href='CLodop_Setup_for_Win32NT.exe' target='_self'></a>,</font>";
    var LODOP;

    let div = document.createElement('div');
    div.setAttribute('id', 'lodop-error-tips');
    let t = 'Error';

    // t=document.createTextNode("CLICK ME");
    // document.body.appendChild(div);

    try {
        var ua = navigator.userAgent;
        var isIE = !!(ua.match(/MSIE/i)) || !!(ua.match(/Trident/i));
        if (needCLodop()) {
            try {
                // reactgetLodop  CLODOPwindow
                LODOP = window.getCLodop();
            } catch (err) {}
            if (!LODOP && CLodopJsState !== "complete") {
                if (CLodopJsState === "loading") alert("."); else alert("CLodopjsloadCLodop.");
                return;
            }
            if (!LODOP) {
                // document.body.innerHTML = strCLodopInstall_1 + (CLodopIsLocal ? strCLodopInstall_2 : "") + strCLodopInstall_3 + document.body.innerHTML;
                t = document.createTextNode(strCLodopInstall_1 + (CLodopIsLocal ? strCLodopInstall_2 : "") + strCLodopInstall_3);
                // console.log(t)
                div.appendChild(t);
                document.body.appendChild(div);
                return;
            } else {
                console.log(window.CLODOP.CVERSION);
                if (window.CLODOP.CVERSION < "3.0.0.1") {
                    // document.body.innerHTML = strCLodopUpdate + document.body.innerHTML;
                    t = document.createTextNode(strCLodopUpdate);
                    div.appendChild(t);
                    document.body.appendChild(div);
                }
                if (oEMBED && oEMBED.parentNode)
                    oEMBED.parentNode.removeChild(oEMBED); //
                if (oOBJECT && oOBJECT.parentNode)
                    oOBJECT.parentNode.removeChild(oOBJECT);
            }
        } else {
            var is64IE = isIE && !!(ua.match(/x64/i));
            //==Lodop,:==
            if (oOBJECT || oEMBED) {
                if (isIE)
                    LODOP = oOBJECT;
                else
                    LODOP = oEMBED;
            } else if (!CreatedOKLodopObject) {
                LODOP = document.createElement("object");
                LODOP.setAttribute("width", 0);
                LODOP.setAttribute("height", 0);
                LODOP.setAttribute("style", "position:absolute;left:0px;top:-100px;width:0px;height:0px;");
                if (isIE)
                    LODOP.setAttribute("classid", "clsid:2105C259-1E0C-4534-8141-A753534CB4CA");
                else
                    LODOP.setAttribute("type", "application/x-print-lodop");
                document.documentElement.appendChild(LODOP);
                CreatedOKLodopObject = LODOP;
            } else
                LODOP = CreatedOKLodopObject;
            //==Lodop:==
            if ((!LODOP) || (!LODOP.VERSION)) {
                if (ua.indexOf('Chrome') >= 0)
                    document.body.innerHTML = strHtmChrome + document.body.innerHTML;
                if (ua.indexOf('Firefox') >= 0)
                    document.body.innerHTML = strHtmFireFox + document.body.innerHTML;
                document.body.innerHTML = (is64IE ? strHtm64_Install : strHtmInstall) + document.body.innerHTML;
                return LODOP;
            }
        }
        if (!LODOP.VERSION) {
            if (!needCLodop())
                document.body.innerHTML = (is64IE ? strHtm64_Update : strHtmUpdate) + document.body.innerHTML;
        }
        //===():==
        // LODOP.SET_LICENSES("", "A192C738FD5815279066", "", "");
        // host=="zwglass.net" 
        LODOP.SET_LICENSES("","1C29B4FDBA7B971E8EC94D5A5A6D72919F9","","");     // *.zwglass.net 
        // LODOP.SET_LICENSES("", "13528A153BAEE3A0254B9507DCDE2839", "", "");
        //=======================================================
        return LODOP;
    } catch (err) {
        alert("getLodop:" + err);
    }
}

export default getLodop;
